import React, { useState, useEffect, useRef, useMemo } from 'react';
import {
  Card,
  CardContent,
  Typography,
  Box,
  Skeleton,
  Chip,
} from '@mui/material';
import {
  BarChart,
  Bar,
  XAxis,
  YAxis,
  CartesianGrid,
  LabelList,
  ResponsiveContainer,
  Tooltip,
  Legend,
  Cell,
} from 'recharts';
import { keyframes } from '@emotion/react';

//  Animation dfinie avec keyframes
const highlightAnimation = keyframes`
  0% { transform: scale(1); opacity: 1; }
  50% { transform: scale(1.02); opacity: 0.9; }
  100% { transform: scale(1); opacity: 1; }
`;

//  Fonction de couleur  maintenant fixe selon consigne
const getBarColor = (value, type) => {
  if (type === 'inbound') {
    // Toujours bleu pour inbound (consigne)
    return '#42A5F5'; // Bleu clair
  } else if (type === 'outbound') {
    // Toujours vert pour outbound (consigne)
    return '#66BB6A'; // Vert clair
  }
  return '#8884d8';
};

//  Formatteur de nombres (optionnel)
const formatNumber = (num) => {
  if (num >= 1000) return (num / 1000).toFixed(1) + 'k';
  return num.toString();
};

//  Fonction utilitaire : retourne le jour de la semaine en franais (lun  ven)
const getDayName = (dateStr) => {
  const date = new Date(dateStr);
  const days = ['Lun', 'Mar', 'Mer', 'Jeu', 'Ven', 'Sam', 'Dim'];
  return days[date.getDay()];
};

//  Fonction pour reset des donnes le lundi  8h
const shouldResetData = () => {
  const now = new Date();
  const day = now.getDay(); // 0 = dimanche, 1 = lundi...
  const hour = now.getHours();

  // Si c'est lundi ET avant 8h  pas encore reset
  // Si c'est lundi ET  partir de 8h  on reset
  // Si c'est aprs lundi  on garde les donnes jusqu' la prochaine semaine
  return day === 1 && hour >= 8;
};

function SLABarchart({ slaData = [], wsConnected = false }) {
  const [data, setData] = useState([]);
  const [animate, setAnimate] = useState(false);
  const prevSlaDataRef = useRef();

  //  Reset des donnes chaque lundi  8h
  useEffect(() => {
    if (shouldResetData()) {
      setData([]); // Rinitialiser les donnes
    }
  }, []);

  //  Gestion des nouvelles donnes
  useEffect(() => {
    const prev = prevSlaDataRef.current;

    if (JSON.stringify(slaData) !== JSON.stringify(prev)) {
      // Filtrer pour garder uniquement les 5 derniers jours (lundi  vendredi)
      let filteredData = [...slaData];

      // Ajouter ou mettre  jour les jours de la semaine
      filteredData = filteredData.map(entry => ({
        ...entry,
        dayLabel: getDayName(entry.date), // Ajoute le label "Lun", "Mar", etc.
      }));

      // Ne garder que les 5 derniers jours (si plus de 5, tronquer)
      if (filteredData.length > 5) {
        filteredData = filteredData.slice(-5);
      }

      setData(filteredData);
      setAnimate(true);
      const timer = setTimeout(() => setAnimate(false), 600);
      return () => clearTimeout(timer);
    }

    prevSlaDataRef.current = slaData;
  }, [slaData]);

  //  Couleurs fixes selon consigne
  const inboundColors = useMemo(() => data.map(entry => getBarColor(entry.inbound, 'inbound')), [data]);
  const outboundColors = useMemo(() => data.map(entry => getBarColor(entry.outbound, 'outbound')), [data]);

  //  Affichage placeholder
  if (data.length === 0) {
    return (
      <Card sx={{ backgroundColor: 'background.paper', maxWidth: 1150, width: '100%', borderRadius: 3 }}>
        <CardContent>
          <Typography variant="overline" color="text.secondary" gutterBottom>
            NUMBER OF CALLS
          </Typography>
          <Box sx={{ textAlign: 'center', py: 4 }}>
            <Chip
              label={wsConnected ? "Aucune donne disponible" : "Connexion en cours..."}
              color={wsConnected ? "warning" : "info"}
              size="small"
              sx={{ mb: 2 }}
            />
            <Skeleton variant="rectangular" width="100%" height={350} />
          </Box>
        </CardContent>
      </Card>
    );
  }

  return (
    <Card
      sx={{
        backgroundColor: 'background.paper',
        maxWidth: 1150,
        width: '100%',
        borderRadius: 3,
        ...(animate && {
          animation: `${highlightAnimation} 0.6s ease-in-out`,
        }),
      }}
    >
      <CardContent>
        <Box sx={{ display: 'flex', justifyContent: 'space-between', alignItems: 'center', mb: 1 }}>
          <Typography variant="overline" color="text.secondary">
            NUMBER OF CALLS
          </Typography>
          <Chip label=" Live" size="small" color="success" />
        </Box>
        <div aria-label="Graphique  barres du nombre d'appels entrants et sortants">
          <ResponsiveContainer width="100%" height={400}>
            <BarChart data={data} margin={{ top: 20, right: 20, left: 0, bottom: 0 }}>
              <CartesianGrid strokeDasharray="3 3" stroke="#2f3a49" />
              {/* Utiliser dayLabel au lieu de date */}
              <XAxis dataKey="dayLabel" stroke="#8884d8" tick={{ fill: '#fff' }} />
              <YAxis stroke="#8884d8" tick={{ fill: '#fff' }} />
              <Tooltip
                contentStyle={{
                  backgroundColor: '#1a2333',
                  border: '1px solid #333',
                  borderRadius: 8,
                  color: '#fff',
                }}
              />
              <Legend
                wrapperStyle={{ color: '#fff', paddingTop: 10 }}
                payload={[
                  { value: 'Inbound', type: 'square', color: '#42A5F5' },
                  { value: 'Outbound', type: 'square', color: '#66BB6A' },
                ]}
              />
              <Bar dataKey="inbound" name="Inbound" maxBarSize={80}>
                {data.map((entry, index) => (
                  <Cell key={`inbound-${index}`} fill={inboundColors[index]} />
                ))}
                <LabelList
                  dataKey="inbound"
                  position="top"
                  fill="#fff"
                  fontWeight="bold"
                  fontSize={14}
                  formatter={formatNumber}
                />
              </Bar>
              <Bar dataKey="outbound" name="Outbound" maxBarSize={80}>
                {data.map((entry, index) => (
                  <Cell key={`outbound-${index}`} fill={outboundColors[index]} />
                ))}
                <LabelList
                  dataKey="outbound"
                  position="top"
                  fill="#fff"
                  fontWeight="bold"
                  fontSize={14}
                  formatter={formatNumber}
                />
              </Bar>
            </BarChart>
          </ResponsiveContainer>
        </div>
      </CardContent>
    </Card>
  );
}

export default SLABarchart;